import React, { Component } from 'react';
import TableBoor from '../TableBoor';

class App extends Component {

    constructor(){
      super();
      this.state={ searchData:"a" }
  }
  getSearchProps = ({ params }) => {

    return params.aid; 
  }

  render() {
    // console.log(this.props)
    const { match } = this.props;
    return (
      <div className="App">
          <TableBoor listType = { this.getSearchProps(match)}/>
      </div>
    );
  }
}

export default App;


