import { TabBar } from 'antd-mobile';
import React from 'react';
import List from "../List";
import Search from "../Search";
import {withRouter} from 'react-router-dom';

class TabBarExample extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      searchData:"a",
      selectedTab: 'blueTab',
      hidden: false,
      listType:props.listType||"book"
    };
    console.log("constructor-Boor",props)
    // console.log("constructor",ReactDOM.findDOMNode(this.props.headNode),this.props.headNode)
  }
  getSearchData = (val) =>{
    this.setState({
      searchData:val
    })
  
 }
 componentWillReceiveProps(props) {
   const {listType} = props
  console.log("componentWillReceiveProps-Boor",props)
  // this.setState({ refreshing: true, isLoading: true });
  // console.log("componentWillReceiveProps",1)
  this.setState({listType})
  // console.log("componentWillReceiveProps",2)
  
}

  render() {
    const { location,history} = this.props;
    const { searchData ,listType} = this.state;
    const boxHeight = document.documentElement.clientHeight-50+"px";
    console.log("Boor-render",this.props)
    return (
      <div >
         <Search  searchType={listType} getSearchData = {this.getSearchData} />
         <div  style={{ position: 'fixed', height:boxHeight, width: '100%', bottom: "50" }}>
            <List searchData={searchData} listType={listType}/>
         </div>
         <div style={{ position: 'fixed', height:"50px", width: '100%', bottom: "0" }}>
            <TabBar
              unselectedTintColor="#949494"
              tintColor="#33A3F4"
              barTintColor="white"
              hidden={this.state.hidden}
            >
              <TabBar.Item
                title=""
                key="book"
                icon={<div style={{width: '22px',height: '22px'}} className ="iconfont">&#xe640;</div>
                }
                selectedIcon={<div style={{width: '22px',height: '22px'}} className ="iconfont">&#xe642;</div>
                }
                selected={listType === 'book'}
                onPress={() => {
                    if(listType === 'book'){return}
                    console.log(this.props)
                    history.push(`${window.publicPath}/book`);
                }}
                data-seed="logId"
              >

              </TabBar.Item>
              <TabBar.Item
                icon={
                  <div style={{width: '22px',height: '22px'}} className ="iconfont">&#xe603;</div>
                }
                selectedIcon={
                  <div style={{width: '22px',height: '22px'}} className ="iconfont">&#xe641;</div>
                }
                title=""
                key="movie"
                selected={listType === 'movie'}
                onPress={() => {
                    if(listType === 'movie'){return}
                    history.push(`${window.publicPath}/movie`);
                }}
                data-seed="logId1"
              >
              </TabBar.Item>
              <TabBar.Item
                icon={
                  <div style={{width: '22px',height: '22px'}} className ="iconfont">&#xe62e;</div>
                }
                selectedIcon={
                  <div style={{width: '22px',height: '22px'}} className ="iconfont">&#xe643;</div>
                }
                title=""
                key="music"
                selected={listType === 'music'}
                onPress={() => {
                    if(listType === 'music'){return}

                    history.push(`${window.publicPath}/music`);
                }}
              >
              </TabBar.Item>
            </TabBar>
          </div>
      </div>
    
    );
  }
}

export default withRouter(TabBarExample);


